import React from 'react';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import Card from '@material-ui/core/Card';
import CardContent from '@material-ui/core/CardContent';
import CardMedia from '@material-ui/core/CardMedia';
import Typography from '@material-ui/core/Typography';
import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import CardActionArea from '@material-ui/core/CardActionArea';
import {Grid} from '@material-ui/core';

const styles = theme => ({
  card: {
    display: 'flex',
  },
  details: {
    display: 'flex',
    flexDirection: 'column',
  },
  content: {
    flex: '1 0 auto',
  },
  cover: {
    width: 151,
  },
  controls: {
    display: 'flex',
    alignItems: 'center',
    paddingLeft: theme.spacing.unit,
    paddingBottom: theme.spacing.unit,
  },
  playIcon: {
    height: 38,
    width: 38,
  },
});

class MediaControlCard extends React.Component{
  constructor(props){
    super(props);
    this.state={
      open: false        
    };    
  }

handleClickOpen = () => {
  this.setState({ open: true });
};

handleClose = () => {
  this.setState({ open: false });
};
getImage = (props) => {
  var url = "/img/" + props.data.id + ".jpg";
  return url;
}
DetailsDialog = () => {
  return (
    <div>
      <Dialog
        open={this.state.open}
        onClose={this.handleClose}
        aria-labelledby="alert-dialog-title"
        aria-describedby="alert-dialog-description"
        maxWidth="xl"
      >
        <DialogTitle id="alert-dialog-title" onClose={this.handleClose}>{this.props.data.judul}</DialogTitle>
        <DialogContent>
          <DialogContentText id="alert-dialog-description">
            <Grid container spacing={24}>
              <Grid item xs={12} sm={4} md={4} xl={4}>
                <img src={this.getImage(this.props)} alt="poster" />
              </Grid>
              <Grid item xs={12} sm={8} md={8} xl={8}>
                <Typography variant="h6">
                  Tagar
                </Typography>                   
                <Typography variant="subtitle1">
                  {this.props.data.tag.map((tag, index) => {
                    var isLast = index + 1 === this.props.data.tag.length;
                    return <div style={{display:'inline'}}>
                      {this.props.tag[tag]}
                      {!isLast && (", ")}
                    </div>
                  })}
                </Typography>
                <Typography variant="h6">
                  Keterangan
                </Typography>                
                <Typography variant="subtitle1">
                 {this.props.data.keterangan}
                </Typography>                      
                <Typography variant="h6">
                  Batas daftar
                </Typography>                   
                <Typography variant="subtitle1">
                 {this.props.data.tanggalBatasDaftar}
                </Typography>  
                <Typography variant="h6">
                 Acara 
                </Typography>                   
                <Typography variant="subtitle1">
                  {this.props.data.tanggalAcara}
                </Typography>                                        
              </Grid>              
            </Grid>
          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button onClick={this.handleClose} color="primary" autoFocus>
            Kembali
          </Button>
        </DialogActions>
      </Dialog>
    </div>  
  );
}
render() {
  const { classes, theme } = this.props;

  return (
    <div>
      <this.DetailsDialog />
      <Card className={classes.card} onClick={this.handleClickOpen} elevation={3}>        
        <CardMedia
          className={classes.cover}
          image={this.getImage(this.props)}
          title="Poster"
        />
        <CardActionArea>
          <div className={classes.details}>
            <CardContent className={classes.content}>
              <Typography component="h5" variant="h5">
                {this.props.data.judul}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary">
                {this.props.data.tag.map((tag, index) => {
                  var isLast = index + 1 === this.props.data.tag.length;
                  return <div style={{display:'inline'}}>
                    {this.props.tag[tag]}
                    {!isLast && (", ")}
                  </div>
                })}
              </Typography>
              <Typography variant="subtitle1" color="textSecondary">
                Batas daftar : <strong>{this.props.data.tanggalBatasDaftar}</strong> Acara : <strong>{this.props.data.tanggalAcara}</strong>
              </Typography>          
            </CardContent>
          </div>
        </CardActionArea>
      </Card>
    </div>
  );
}
}
MediaControlCard.propTypes = {
  classes: PropTypes.object.isRequired,
  theme: PropTypes.object.isRequired,
};

export default withStyles(styles, { withTheme: true })(MediaControlCard);