import React, { Component } from 'react';
import {
  Paper,
  Typography,
  Button,
  Grid,
  Card,
  CardContent,
  CardMedia,
  TextField,
  Chip,
  SwipeableDrawer
} from '@material-ui/core';
// import Fab from '@material-ui/core/Fab';
import { withStyles } from '@material-ui/core/styles';


import PropTypes from 'prop-types';

// import PropTypes, { instanceOf } from 'prop-types';


class SearchChip extends Component {
    constructor(props){
        super(props);
        this.state={
            facility: [{
                isSelected:false,
                index: 0,
                data:{facility:'loading'}
            }],            
            right: false            
        };
        this.handleChange = this.handleChange.bind(this);     
        this.AllFacility = this.AllFacility.bind(this);
        this.SelectFacility = this.SelectFacility.bind(this);
    }
    componentDidMount(){
      console.log(this.props);
      var facility = [];
      this.props.data.map((a, index) => {
        facility.push({
          isSelected: false,
          index: index,
          data: {
            facility: a
          }
        });
      });
      this.setState({facility: facility});
    }
    asdf = () => {
      var arrSelected = [];
      this.state.facility.filter(facility => {
        return(facility.isSelected)
      }).map(facility => {
        arrSelected.push(facility.index);
      })
      this.props.sendSelectedArr(arrSelected);
    }
      SelectFacility(){
        return(
          <div>
            {this.state.facility.filter(facility => {
              return(facility.isSelected);
            }).map((facility) => {
              return(
                <Chip
                label={facility.data.facility}
                onDelete={() => {
                  let tempArrEl = this.state.facility;
                  tempArrEl[facility.index].isSelected = false;
                  this.setState({tempArrEl,});
                  this.asdf();
                }}
                color="primary"
                style={{margin:4}}
              />
              );
            })}
          </div>
        );
      } 
      AllFacility(){
        return(
          <div>
            {this.state.facility.filter((facility) => {
              if(facility.data.facility.toLowerCase().search(this.props.filterFacility.toLowerCase()) !== -1 && facility.isSelected === false){
                return true;
              }
              return false;
            }).map((facility) => {
              return(
                <Chip
                  // avatar={<Avatar>MB</Avatar>}
                  label={facility.data.facility}
                  onClick={() => {
                    let tempArrEl = this.state.facility;
                    tempArrEl[facility.index].isSelected = true;
                    this.setState({tempArrEl,});
                    this.asdf();
                  }}
                  style={{margin:4}}
                  // className={classes.chip}
                />
              );
            })}
          </div>
        );
      }
    handleChange(e) {
        this.setState({
          [e.target.name]: e.target.value
        });
        // console.log(this.state.email);
        // console.log(this.state.password);
    }
    render(props) {      
      return (
        <div>
          <this.SelectFacility />
          <this.AllFacility />
        </div>
      );
    }
}

// export default SignIn
SearchChip.propTypes = {
    classes: PropTypes.object.isRequired,
    theme: PropTypes.object.isRequired,
  };
  
export default (SearchChip);
