import React, { Component } from 'react';
import { withStyles } from '@material-ui/core/styles';
import {
//   Paper,
//   Typography,
  TextField,
//   Button,
//   Dialog,
//   DialogTitle,
//   DialogContent,
//   DialogContentText,
//   DialogActions,
  Grid
} from '@material-ui/core';
import {Typography, Button} from '@material-ui/core/';
import {Link} from 'react-router-dom';
import MediaControlCard from './components/MediaControlCard';
import PropTypes from 'prop-types';
import HomeSearch from './components/HomeSearch';
import SearchChip from './components/SearchChip';
// import axios from 'axios';
class App extends Component {

  constructor(props){
    super(props);
    this.state = {
      keyword: "",
      selectedArr: []
    };
    this.handleChange = this.handleChange.bind(this);
    this.sendSelectedArr = this.sendSelectedArr.bind(this);
  }  
  handleChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }
  sendSelectedArr (arr){
    this.setState({selectedArr: arr});
  }
  render() {
    const { classes } = this.props;
    var lomba = ['Competitive Programming', 'Caputre the Flag', 'Hackathon', 'Business Plan', 'Game Development', 'Robotic'];
    var eventWorkshop =['Data Science', 'Software Engineering', 'Android', 'Cloud', 'MongoDb', 'Elastic Search', 'Security', 'Java', 'Javascript', 'Reactjs'];    
    var data = [
      {
        data : {
          id: 0,
          judul: "Compfest X",
          tag: [0, 1, 2, 3, 4],
          keterangan: "The quick brown fox jumps over the lazy dog",
          tanggalBatasDaftar: "16 Mei '19",
          tanggalAcara: "20 Juni '19"          
        }
      },
      {
        data : {
          id: 1,
          judul: "ACM ICPC",
          tag: [0],
          keterangan: "The quick brown fox jumps over the lazy dog",
          tanggalBatasDaftar: "16 Juni '19",
          tanggalAcara: "20 Agustus '19"          
        }
      }, 
      {
        data : {
          id: 2,
          judul: "IT Today",
          tag: [1],
          keterangan: "The quick brown fox jumps over the lazy dog",
          tanggalBatasDaftar: "16 Januari '19",
          tanggalAcara: "20 Februari '19"          
        }
      },
      {
        data : {
          id: 3,
          judul: "Arkavidia",
          tag: [0, 1, 2],
          keterangan: "The quick brown fox jumps over the lazy dog",
          tanggalBatasDaftar: "16 September '19",
          tanggalAcara: "20 Desember '19"          
        }
      }                
    ]
    return (
    <div className='container'>
      <div className='header80'>
        <Grid container spacing={24} style={{width:'60%', margin:'auto'}}>
          <Grid item xs={12} sm={12} lg={12} xl={12}>
            <HomeSearch onChange={this.handleChange} value={this.state.keyword} name="keyword" placeholder="Cari Event, Workshop, dan Lomba" />
          </Grid>        
        </Grid>
        <Grid container spacing={24} style={{width:'80%', margin:'auto'}}>
          <Grid item xs={6} sm={6} lg={6} xl={6}>
            <Typography variant="h6" style={{textShadow:'1px 1px #0200004d', color:'#eee', textAlign:'center'}}>
              Event dan Workshop
            </Typography>
            <SearchChip filterFacility={this.state.keyword} data={eventWorkshop} sendSelectedArr={this.sendSelectedArr} />            
          </Grid>
          <Grid item xs={6} sm={6} lg={6} xl={6}>
            <Typography variant="h6" style={{textShadow:'1px 1px #0200004d', color:'#eee', textAlign:'center'}}>
              Lomba
            </Typography>    
            <SearchChip filterFacility={this.state.keyword} data={lomba} sendSelectedArr={this.sendSelectedArr} />
          </Grid>          
        </Grid>        
      </div>      
      <div className='content80'>
        {/* <div className={classes.cardContainer}>
          <div className={classes.asdf}><HomeCard data={data[0].data} /></div>
          <div className={classes.asdf}><HomeCard data={data[1].data} /></div>
          <div className={classes.asdf}><HomeCard data={data[2].data} /></div>
          <div className={classes.asdf}><HomeCard data={data[3].data} /></div>
        </div> */}
        {data.filter(d => {
          return this.state.selectedArr.length === 0? true: d.data.tag.some(r=> this.state.selectedArr.includes(r))
        }).map(d => {
          return <div className={classes.asdf}><MediaControlCard data={d.data} tag={lomba} /></div>
        })}
        {/* <div className={classes.asdf}><MediaControlCard data={data[0].data} tag={lomba} sendSelectedArr={this.sendSelectedArr} /></div>
        <div className={classes.asdf}><MediaControlCard data={data[1].data} tag={lomba} sendSelectedArr={this.sendSelectedArr} /></div>
        <div className={classes.asdf}><MediaControlCard data={data[2].data} tag={lomba} sendSelectedArr={this.sendSelectedArr} /></div>
        <div className={classes.asdf}><MediaControlCard data={data[3].data} tag={lomba} sendSelectedArr={this.sendSelectedArr} /></div> */}
      </div>    
    </div>);
  }
}
const styles = {
  cardContainer: {
      display: 'flex',
      flexDirection: 'row',
      flexWrap: 'wrap',
      width: '100%',
      alignItems: 'stretch'
  },
  asdf: {
    margin: '8px',
    width: 'auto'
  },
  notchedOutline: {
    borderWidth: "1px",
    borderColor: "white !important",    
  },
  multilineColor: {
    color: 'white'
  },
  formLabel: {
    color: 'white'
  }
};
App.propTypes = {
  classes: PropTypes.object.isRequired,
};
export default withStyles(styles)(App);
