import React, { Component } from "react";
import { Grid } from "@material-ui/core";

class Footer extends Component {
  render() {
		if(!window.matchMedia("(min-width: 500px)").matches){
			return null;
		}
    return (
      <div style={{ 
	  backgroundColor:'rgba(0, 0, 0, 0.08)'
	  }}>
	  	<div style={{
			  padding:'16px'
		  }}>
		  	<div>
				<div style={{float:'left', marginBottom: '12px'}}>
					<img src="/img/himti.png" alt="hackjacklogo" style={{width:'42px',height:
					'42px'}}/>
				</div>
				<div 
				style={{paddingLeft:8, color:'#656565',float:'left', marginLeft:'4px'}}>
					&copy; 2019 Himpunan Mahasiswa Teknik Informatika UIN Syarif Hidayatullah Jakarta <br/>
					<a href='/'>Syarat dan Ketentuan</a> - <a href='/'>Hubungi Kami</a> - <a href='/'>Kirim Umpan Balik</a>
				</div>
		  	</div>
		</div>        
      </div>
    );
  }
}

export default Footer;